    },
    remove() {
      this.product = null;
    },
    addprod() {
      if (this.product != null) {
        if (this.productlst != []) {
          if (this.productlst.some((s) => s.cod == this.product)) {
            this.productlst
              .filter((s) => s.cod == this.product)
              .forEach((s) => (s.count = s.count + 1));
            this.product = null;
          } else {
            let toadd = this.people.find((t) => t.cod == this.product);
            toadd.count = 1;
            this.productlst.push(toadd);
            this.product = null;
          }
        }
      }
    },
